    let hoursDiff = NewDate.getHours() - NewDate.getTimezoneOffset() / 60;
    let minutesDiff = (NewDate.getHours() - NewDate.getTimezoneOffset()) % 60;
    NewDate.setHours(hoursDiff);
    NewDate.setMinutes(minutesDiff);
